footer {
  @include white-frosty-gradient-down;
  padding: 3rem 0 1rem;

  .fa {
    color: $medium-green;
  }

  a {
    color: $medium-green;
    text-decoration: underline;

    &:hover, &:active, &:focus {
      color: $dark-gray;
    }
  }

  li {
    margin-bottom: .5em;
  }

  .row:last-child {
    margin-top: 4em;
    margin-bottom: 0;
    font-size: 12px;

    a {
      text-decoration: underline;
      color: $dark-gray;

      &:hover, &:active, &:focus {
        color: $medium-green;
      }
    }
  }
}

@media only screen and (max-width: 425px) {
  .brand-logo {
    display: none;
  }
  .size-medium {
    text-align: center;
  
  }
}



